// @flow

import React, { Component } from "react";

import MODERN_REGIONS from "./regions.json";
const MARGINS = { top: 10, left: 50, right: 10 };
const SIZE = {
  width: window.innerWidth - MARGINS.left - MARGINS.right,
  height: 55
};

export default class OverviewMap extends Component<{}, State> {
  handleClick(evt) {
    // console.log("overview click:", {
    //   x: (evt.nativeEvent.pageX - MARGINS.left) / SIZE.width,
    //   y: (evt.nativeEvent.pageY - MARGINS.top) / SIZE.height
    // });
    this.props.onClick({
      xRatio: (evt.nativeEvent.pageX - MARGINS.left) / SIZE.width,
      yRatio: (evt.nativeEvent.pageY - MARGINS.top) / SIZE.height
    });
  }

  render() {
    return (
      <div id="overview" className="Set3" onClick={this.handleClick.bind(this)}>
        <div id="bounds" style={this.props.viewbox} />
        {MODERN_REGIONS.map((region, i) => {
          const { name, paddingTop, ...style } = region;
          return (
            <div key={i} title={name} className={`q${i % 12}-12`} style={style}>
              <span style={{ paddingTop: paddingTop }}>{name}</span>
            </div>
          );
        })}
        {
          // <div id="yangtze" className="q0-12">
          //   <span>Yangtze</span>
          // </div>
          // <div id="coast" className="q1-12">
          //   <span>China Coast</span>
          // </div>
          // <div id="vietnam" className="q2-12">
          //   <span>Vietnam</span>
          // </div>
          // <div id="thailand" className="q3-12">
          //   <span>Thailand</span>
          // </div>
          // <div id="indonesia" className="q4-12">
          //   <span>Indonesia</span>
          // </div>
          // <div id="malaysia" className="q5-12">
          //   <span>Malaysia</span>
          // </div>
          // <div id="bengal" className="q6-12">
          //   <span>Bengal Sea</span>
          // </div>
          // <div id="srilanka" className="q7-12" title="Sri Lanka">
          //   <span>Sri Lanka</span>
          // </div>
          // <div id="india" className="q8-12">
          //   <span>India</span>
          // </div>
          // <div id="pakistan" className="q9-12">
          //   <span>Pakistan</span>
          // </div>
          // <div id="iran" className="q10-12">
          //   <span>Iran</span>
          // </div>
          // <div id="oman" className="q11-12">
          //   <span>Oman</span>
          // </div>
          // <div id="yemen" className="q0-12">
          //   <span>Yemen</span>
          // </div>
          // <div id="somalia" className="q1-12">
          //   <span>Somalia</span>
          // </div>
          // <div id="kenya" className="q2-12">
          //   <span>Kenya</span>
          // </div>
          // <div id="tanzania" className="q3-12">
          //   <span>Tanzania</span>
          // </div>
          // <div id="mozambique" className="q4-12" title="Mozambique">
          //   <span>Mozambique</span>
          // </div>
        }{" "}
      </div>
    );
  }
}



