import React, { useState, useCallback, useRef } from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import Avatar from "@material-ui/core/Avatar";
import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import Typography from "@material-ui/core/Typography";
import { blue } from "@material-ui/core/colors";
import AddCommentIcon from "@material-ui/icons/AddComment";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import BeenHereIcon from "@material-ui/icons/Beenhere";
import { Map, LayersControl, TileLayer, Marker } from "react-leaflet";
import { Icon as LeafletIcon } from "leaflet";

const { BaseLayer } = LayersControl;

function useHookWithRefCallback() {
  const ref = useRef(null);
  const setRef = useCallback(node => {
    if (node) {
      setTimeout(
        () =>
          node.leafletElement.flyTo(node.props.center, 11, {
            animate: true,
            duration: 1.5
          }),
        1000
      );
    }

    ref.current = node;
  }, []);

  return [setRef];
}

const useStyles = makeStyles(theme => ({
  card: {
    maxWidth: 345
  },
  media: {
    height: 0,
    paddingTop: "56.25%" // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest
    })
  },
  expandOpen: {
    transform: "rotate(180deg)"
  },
  avatar: {
    backgroundColor: blue[500]
  },
  commentsContainer: {
    height: "calc(100vh - 413px)"
  }
}));

export const MapMarker = new LeafletIcon({
  iconUrl: require("./marker.svg"),
  iconRetinaUrl: require("./marker.svg"),
  iconAnchor: [10, 20],
  iconSize: [20, 20]
});

// id: 2
// inEnglish: "The Imperial Palace City"
// latitude: 32.0383512
// longitude: 118.8150177
// mapText: "皇城"
// modernNameChinese: "明故宫"
// modernNameEnglish: "Ming Palace"
// urlForDetails: "https://en.wikipedia.org/wiki/Ming_Palace"
// urlLanguage: "en"
// xPixel: 105513
// yPixel: -1863

export default function LabelDisplay(props) {
  const classes = useStyles();
  const [expanded, setExpanded, satTiles] = useState(false);
  const [ref] = useHookWithRefCallback();

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  if (!props.label) return null;
  const l = props.label;

  return (
    <div>
      <CardHeader
        avatar={
          <Avatar
            aria-label="recipe"
            className={classes.avatar}
            title={`ID: ${l.id}`}
          >
            <BeenHereIcon />
          </Avatar>
        }
        title={l.mapText}
        subheader={l.inEnglish}
      />
      <CardMedia className={classes.media}>
        {l.latitude && l.longitude && (
          <Map center={[l.latitude, l.longitude]} zoom={2} ref={ref}>
            <LayersControl position="topright">
              <BaseLayer name="Map" checked={!satTiles}>
                <TileLayer
                  attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                  url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
              </BaseLayer>
              <BaseLayer name="Satellite" checked={satTiles}>
                <TileLayer
                  // attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                  url="https://api.mapbox.com/v4/mapbox.satellite/{z}/{x}/{y}.png?access_token=pk.eyJ1IjoiY2FycGllZGllbSIsImEiOiJjazN5azllN3gwbWp1M2pxY2l1c3ZkZTNzIn0.tVQt4IChZNREXzO7ieVkbw"
                  // url="https://api.mapbox.com/styles/v1/mapbox/satellite-streets-v11/tiles/256/${z}/${x}/${y}?access_token=pk.eyJ1IjoiY2FycGllZGllbSIsImEiOiJjazN5azllN3gwbWp1M2pxY2l1c3ZkZTNzIn0.tVQt4IChZNREXzO7ieVkbw"
                />
              </BaseLayer>
            </LayersControl>
            {l.latitude && (
              <Marker position={[l.latitude, l.longitude]} icon={MapMarker} />
            )}
          </Map>
        )}
      </CardMedia>
      <CardContent>
        {(l.modernNameChinese || l.modernNameEnglish) && (
          <Typography variant="subtitle2" color="textSecondary" component="p">
            Now known as:
          </Typography>
        )}
        <Typography variant="body2" component="p">
          {l.modernNameChinese}
        </Typography>
        <Typography variant="body2" component="p">
          {l.modernNameEnglish}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <Tooltip
          title={`Learn more${l.urlLanguage === "zh" ? " (Chinese)" : ""}`}
          aria-label="Learn more"
          placement="bottom"
        >
          <IconButton
            href={l.urlForDetails}
            target="_blank"
            disabled={!l.urlLanguage}
          >
            <Icon className="fab fa-wikipedia-w" />
          </IconButton>
        </Tooltip>
        <Tooltip
          title="Give Feedback"
          aria-label="Give Feedback"
          placement="bottom"
        >
          <IconButton>
            <AddCommentIcon />
          </IconButton>
        </Tooltip>
        <Tooltip
          title="See Comments"
          aria-label="See Comments"
          placement="bottom"
        >
          <IconButton
            className={clsx(classes.expand, {
              [classes.expandOpen]: expanded
            })}
            onClick={handleExpandClick}
            aria-expanded={expanded}
          >
            <ExpandMoreIcon />
          </IconButton>
        </Tooltip>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent className={classes.commentsContainer}>
          <Typography paragraph>TBD</Typography>
        </CardContent>
      </Collapse>
    </div>
  );
}



