// @flow

import React, { Component } from "react";
import { CRS, Icon } from "leaflet";
import { Map, Circle, Marker } from "react-leaflet";
import Fab from "@material-ui/core/Fab";
import Tooltip from "@material-ui/core/Tooltip";
import AddIcon from "@material-ui/icons/Add";

import ZoomifyLayer from "./ZoomifyLayer";
import OverviewMap from "./OverviewMap";
import Drawer from "./Drawer";
import LabelDisplay from "./LabelDisplay";
import LabelEditor from "./LabelEditor";
// import FeedbackSnackbar from "./FeedbackSnackbar";
import crosshairsIcon from "./crosshairsIcon";
import apiConfig from "./.api-config.json";

const SHEETY_URL = `${apiConfig.urlBase}/labels`;
const SHEETY_TOKEN = apiConfig.token;
const MAOKUN_URL =
  "https://barbierilow.faculty.history.ucsb.edu/Research/ZhengHeMapZoomify/ZhengHe/";
const MAOKUN_WIDTH = 108401; // (423 * 256) + 113
const MAOKUN_HEIGHT = 4263; // (16 * 256) + 167
const START_PIXEL = [105513, -1863];

type State = {
  lat: number,
  lng: number,
  zoom: number,
  viewbox: object,
  labels: [object],
  adding: boolean,
  message: string
};

export const textHighlight = new Icon({
  iconUrl: require("./icon.svg"),
  iconRetinaUrl: require("./icon.svg"),
  iconAnchor: [20, 20],
  popupAnchor: [0, -30],
  iconSize: [40, 40]
});

export default class MaoKunMap extends Component<{}, State> {
  seCorner = { lat: 0, lng: 0 };

  state = {
    lat: this.toLatLng(START_PIXEL).lat,
    lng: this.toLatLng(START_PIXEL).lng,
    zoom: 6,
    viewbox: {},
    labels: [],
    selected: null,
    adding: false,
    message: "..."
  };

  toLatLng([x, y]) {
    return {
      lat: (-y / MAOKUN_HEIGHT) * this.seCorner.lat,
      lng: (x / MAOKUN_WIDTH) * this.seCorner.lng
    };
  }

  toPixels({ lat, lng }) {
    return [
      (lng / this.seCorner.lng) * MAOKUN_WIDTH,
      (lat / this.seCorner.lat) * MAOKUN_HEIGHT
    ];
  }

  setViewbox({ target }) {
    const map = target; // Leaflet instance
    const latlngBounds = map.getBounds();
    const { lat, lng } = map.getCenter();
    const zoom = map.getZoom();

    const viewbox = {
      top:
        Math.max(0, latlngBounds._northEast.lat / this.seCorner.lat) * 100 +
        "%",
      bottom:
        Math.max(0, 1 - latlngBounds._southWest.lat / this.seCorner.lat) * 100 +
        "%",
      left:
        Math.max(0, latlngBounds._southWest.lng / this.seCorner.lng) * 100 +
        "%",
      right:
        Math.max(0, 1 - latlngBounds._northEast.lng / this.seCorner.lng) * 100 +
        "%"
    };

    this.setState({ viewbox, lat, lng, zoom });
  }

  panTo({ xRatio, yRatio }) {
    const zoom = this.refs.map.leafletElement.getZoom();
    this.setState({
      lat: this.seCorner.lat * yRatio,
      lng: this.seCorner.lng * xRatio,
      zoom
    });
  }

  async requestLabels() {
    const response = await fetch(SHEETY_URL, {
      headers: { Authorization: `Bearer ${SHEETY_TOKEN}` }
    });

    if (!response.ok) {
      throw new Error("HTTP-Error: " + response.status);
    }

    const { labels } = await response.json();
    this.setState({ labels });
  }

  selectLabel(selected) {
    if (this.state.selected !== selected) this.setState({ selected });
  }

  handleAddClick() {
    this.setState({ adding: true });
    setTimeout(() => document.getElementById("mapText").focus(), 200);
  }

  handleSave(newLabel) {
    this.setState({
      labels: this.state.labels.concat([newLabel]),
      adding: false
    });
  }

  handleCancelClick() {
    this.setState({ adding: false });
  }

  componentDidMount() {
    const map = this.refs.map.leafletElement;
    map.on({
      move: this.setViewbox.bind(this),
      zoom: this.setViewbox.bind(this)
    });

    const maxZoom = Math.ceil(
      Math.max(Math.log2(MAOKUN_WIDTH / 256), Math.log2(MAOKUN_HEIGHT / 256))
    );

    this.seCorner = map.unproject([MAOKUN_WIDTH, MAOKUN_HEIGHT], maxZoom);
    this.setState(this.toLatLng(START_PIXEL));
    this.requestLabels();
  }

  render() {
    const {
      lat,
      lng,
      zoom,
      viewbox,
      labels,
      selected,
      adding
      // message
    } = this.state;
    const center = { lat, lng };
    const pixels = this.toPixels(center).map(n => parseInt(n, 10));

    return (
      <div>
        {
          // <FeedbackSnackbar variant="success" message={message} />
        }
        <Map
          className="background"
          center={center}
          zoom={zoom}
          crs={CRS.Simple}
          ref="map"
          onMove={this.handleMove}
          onClick={() => this.selectLabel(null)}
        >
          <ZoomifyLayer
            attribution="<a href='https://en.wikipedia.org/wiki/Wubei_Zhi'>Mao Yuanyi</a> & <a href='https://barbierilow.faculty.history.ucsb.edu/Research/ZhengHeMapZoomify/ZhengHe.htm'>Prof. Anthony Barbieri</a>"
            url={MAOKUN_URL}
            width={MAOKUN_WIDTH}
            height={MAOKUN_HEIGHT}
          />
          {!adding &&
            labels.map(label => {
              if (!label.xPixel || !label.yPixel) return null;
              return (
                <Circle
                  key={label.id}
                  className="label"
                  center={this.toLatLng([label.xPixel, label.yPixel])}
                  radius={
                    (!this.state.selected ||
                      label.id === this.state.selected) &&
                    0.4
                  }
                  color={
                    label.urlForDetails && label.inEnglish ? "blue" : "yellow"
                  }
                  onClick={() => this.selectLabel(label.id)}
                  bubblingMouseEvents={false}
                ></Circle>
              );
            })}
          {adding && <Marker position={center} icon={crosshairsIcon} />}
        </Map>

        <OverviewMap viewbox={viewbox} onClick={this.panTo.bind(this)} />
        <Drawer open={selected !== null}>
          <LabelDisplay
            label={selected && labels.find(l => l.id === selected)}
          />
        </Drawer>
        <Drawer open={adding}>
          <LabelEditor
            pixels={pixels}
            onCancel={this.handleCancelClick.bind(this)}
            onSave={this.handleSave.bind(this)}
          />
        </Drawer>
        <Tooltip
          title="Add a Translation"
          aria-label="Add a Translation"
          placement="left"
        >
          <Fab
            id="new"
            color="primary"
            onClick={this.handleAddClick.bind(this)}
          >
            <AddIcon />
          </Fab>
        </Tooltip>
      </div>
    );
  }
}



