import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import FormControl from "@material-ui/core/FormControl";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import Switch from "@material-ui/core/Switch";
import TextField from "@material-ui/core/TextField";
import { blue } from "@material-ui/core/colors";
import AddBoxIcon from "@material-ui/icons/AddBox";
import { Map, LayersControl, TileLayer, Marker } from "react-leaflet";
import { Icon as LeafletIcon } from "leaflet";
import apiConfig from "./.api-config.json";

const { BaseLayer } = LayersControl;
const SHEETY_URL = `${apiConfig.urlBase}/labels`;
const SHEETY_TOKEN = apiConfig.token;
const RE_ONLY_ZH = /^[\u2E80-\u2EFF\u3000-\u303F\u31C0-\u31EF\u3200-\u32FF\u3300-\u33FF\u3400-\u4DBF\u4E00-\u9FFF\uF900-\uFAFF\uFE30-\uFE4F]+$/; // /^\p{Han}+$/;
const DEFAULT_LATLNG = { lat: 25, lng: 85 };
const BLANK_FORM = {
  mapText: "",
  inEnglish: "",
  modernNameChinese: "",
  modernNameEnglish: "",
  urlForDetails: "",
  urlLanguage: ""
};

const useStyles = makeStyles(theme => ({
  card: {
    maxWidth: 345
  },
  media: {
    height: 0,
    paddingTop: "56.25%" // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest
    })
  },
  expandOpen: {
    transform: "rotate(180deg)"
  },
  avatar: {
    backgroundColor: blue[500]
  },
  cardContent: {
    overflowY: "scroll"
  },
  cardActions: {
    borderTop: "thin solid #eee"
  },
  textField: {
    width: "100%"
  },
  formControl: {
    width: "100%"
  }
}));

export const MapMarker = new LeafletIcon({
  iconUrl: require("./marker.svg"),
  iconRetinaUrl: require("./marker.svg"),
  iconAnchor: [10, 20],
  iconSize: [20, 20]
});

export default function LabelEditor(props) {
  const classes = useStyles();
  const [satelliteLayer] = useState(false);
  const [latlng, setLatlng] = useState(DEFAULT_LATLNG);
  const [zoom, setZoom] = useState(2);
  const [includeLatlng, setIncludeLatlng] = useState(false);
  const [form, setForm] = useState(BLANK_FORM);

  const validInput = RE_ONLY_ZH.test(form.mapText);

  let handleMove = evt => {
    // ignore events triggered by controlled chnages of state
    if (!evt.originalEvent) return;

    // Update state when the user drags the map
    setZoom(evt.target.getZoom());
    setLatlng(evt.target.getCenter());
  };

  const handleChange = evt =>
    setForm({ ...form, [evt.target.id]: evt.target.value });

  const handleLatlngPaste = evt => {
    const coords = evt.target.value
      .split(/[,\s]+/)
      .map(string => parseFloat(string.trim()) || 0)
      .slice(0, 2);

    if (coords.length < 2) return;
    setLatlng({
      lat: Math.max(-90, Math.min(90, coords[0])),
      lng: Math.max(-180, Math.min(180, coords[1]))
    });
  };

  const handleSave = async () => {
    const label = {
      ...form,
      latitude: latlng.lat === 25 ? null : latlng.lat,
      longitude: latlng.lng === 85 ? null : latlng.lng,
      xPixel: props.pixels[0],
      yPixel: -props.pixels[1]
    };

    const response = await fetch(SHEETY_URL, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${SHEETY_TOKEN}`,
        "content-type": "application/json"
      },
      body: JSON.stringify({ label })
    })
      .catch(err => {
        console.error(err);
        // TBD: display error in Snackbar

        // close the Drawer UI
        props.onCancel();
      })
      .finally(() => {
        setLatlng(DEFAULT_LATLNG);
        setForm(BLANK_FORM);
      });

    if (!response.ok) {
      // TBD: display error in Snackbar
    }

    const body = await response.json();
    props.onSave(body.label);
  };

  const latlngString = `${Math.round(latlng.lat * 1000) / 1000}, ${Math.round(
    latlng.lng * 1000
  ) / 1000}`;

  return (
    <div>
      <CardHeader
        avatar={
          <Avatar className={classes.avatar} title="New label">
            <AddBoxIcon />
          </Avatar>
        }
        title="New Label"
      />
      {includeLatlng && (
        <CardMedia className={classes.media}>
          <Map center={latlng} zoom={zoom} onMove={handleMove}>
            <LayersControl position="topright">
              <BaseLayer name="Map" checked={!satelliteLayer}>
                <TileLayer
                  attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                  url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
              </BaseLayer>
              <BaseLayer name="Satellite" checked={satelliteLayer}>
                <TileLayer
                  attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                  url="https://api.mapbox.com/v4/mapbox.satellite/{z}/{x}/{y}.png?access_token=pk.eyJ1IjoiY2FycGllZGllbSIsImEiOiJjazN5azllN3gwbWp1M2pxY2l1c3ZkZTNzIn0.tVQt4IChZNREXzO7ieVkbw"
                />
              </BaseLayer>
            </LayersControl>
            <Marker position={latlng} icon={MapMarker} />
          </Map>
        </CardMedia>
      )}
      <CardContent
        className={classes.cardContent}
        style={{
          height: includeLatlng ? "calc(100vh - 368px)" : "calc(100vh - 200px)"
        }}
      >
        <TextField
          id="pixels"
          disabled
          value={props.pixels.toString()}
          label="Pixels"
          helperText="Drag the Mao Kun map to place the crosshairs above the map label"
          margin="dense"
          variant="filled"
          className={classes.textField}
        />
        <TextField
          id="mapText"
          required
          value={form.mapText}
          onChange={handleChange}
          error={form.mapText.length > 0 && !validInput}
          label="Map Text"
          helperText="Copy what is written on the map"
          margin="dense"
          variant="filled"
          className={classes.textField}
        />
        <TextField
          id="inEnglish"
          value={form.inEnglish}
          onChange={handleChange}
          label="In English"
          helperText="An appropriate translation or transliteration of the map text"
          margin="dense"
          variant="filled"
          className={classes.textField}
        />
        <TextField
          id="modernNameChinese"
          value={form.modernNameChinese}
          onChange={handleChange}
          label="Modern Name in Chinese"
          margin="dense"
          variant="filled"
          className={classes.textField}
        />
        <TextField
          id="modernNameEnglish"
          value={form.modernNameEnglish}
          onChange={handleChange}
          label="Modern Name in English"
          margin="dense"
          variant="filled"
          className={classes.textField}
        />
        <TextField
          id="urlForDetails"
          value={form.urlForDetails}
          onChange={handleChange}
          label="Website with More Information"
          helperText="A URL (e.g. perhaps to a Wikipedia article)"
          margin="dense"
          variant="filled"
          className={classes.textField}
        />
        <FormControl variant="filled" className={classes.formControl}>
          <InputLabel htmlFor="filled-age-native-simple">
            Webpage Language
          </InputLabel>
          <Select
            native
            value={form.urlLanguage}
            onChange={handleChange}
            inputProps={{
              id: "urlLanguage"
            }}
          >
            <option value="" />
            <option value="en">English</option>
            <option value="zh">Chinese</option>
          </Select>
        </FormControl>

        <FormControlLabel
          control={
            <Switch
              checked={includeLatlng}
              onChange={() => setIncludeLatlng(!includeLatlng)}
              value="includeLatlng"
              color="primary"
            />
          }
          label="Mark on globe"
        />
        {includeLatlng && (
          <TextField
            id="latlng"
            value={latlngString}
            onChange={handleLatlngPaste}
            label="Global Coordinates"
            helperText="Drag the world map, above, to place the crosshairs in the right geographic location"
            margin="dense"
            variant="filled"
            className={classes.textField}
          />
        )}
      </CardContent>
      <CardActions disableSpacing className={classes.cardActions}>
        <Button color="primary" disabled={!validInput} onClick={handleSave}>
          Save
        </Button>
        <Button color="secondary" onClick={props.onCancel}>
          Discard
        </Button>
      </CardActions>
    </div>
  );
}



